@font-face {
  font-family: "Monseratt";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(Montserrat-Regular), url("../fonts/Montserrat-Regular.woff2"),
    format("woff2"), url("../fonts/Montserrat-Regular.woff"), format("woff");
}

