@font-face {
  font-family: "Monseratt";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local(Montserrat-Regular), url("../fonts/Montserrat-Regular.woff2"), format("woff2"), url("../fonts/Montserrat-Regular.woff"), format("woff"); }

@font-face {
  font-family: "Monseratt";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local(Montserrat-Medium), url("../fonts/Montserrat-Medium.woff2"), format("woff2"), url("../fonts/Montserrat-Medium.woff"), format("woff"); }

@font-face {
  font-family: "Monseratt";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local(Montserrat-SemiBold), url("../fonts/Montserrat-SemiBold.woff2"), format("woff2"), url("../fonts/Montserrat-SemiBold.woff"), format("woff"); }

@font-face {
  font-family: "Monseratt";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local(Montserrat-Bold), url("../fonts/Montserrat-Bold.woff2"), format("woff2"), url("../fonts/Montserrat-Bold.woff"), format("woff"); }

.body {
  font-family: "Monserrat", arial, sans-serif;
  font-size: 16px;
  color: #2c2e3f;
  background-color: #ffffff; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  overflow: hidden; }

.link {
  text-decoration: none; }

.list {
  list-style: none;
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }
